
import Vue from 'vue'
import Component from 'vue-class-component'
import Orders from '@/modules/orders/components/list/Orders.vue'
import Storage from '@/models/Storage'
import { benchmarking } from '@/modules/benchmarking/store/namespace'
import {
  SET_IS_IN_BENCHMARKING_MODE,
  SET_NUMBER_OF_ORDERS_REQUESTED,
  SetIsInBenchmarkingModePayload,
  SetNumberOfOrdersRequestedPayload
} from '@/modules/benchmarking/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import { GET_NUMBER_OF_ORDERS_REQUESTED } from '@/modules/benchmarking/store/getters'
import { Watch } from 'vue-property-decorator'
import { Route } from 'vue-router'
import { updateMetaRefreshContent } from '@/modules/shared/helpers'
const DEFAULT_NUMBER_OF_REQUESTED_ORDERS = 50

@Component({
  name: 'Benchmarking',
  components: {
    Orders
  }
})
export default class Benchmarking extends Vue {
  @benchmarking.Action(SET_IS_IN_BENCHMARKING_MODE)
  setIsInBenchmarkingMode!: StoreAction<SetIsInBenchmarkingModePayload>

  @benchmarking.Action(SET_NUMBER_OF_ORDERS_REQUESTED)
  setNumberOfOrdersRequested!: StoreAction<SetNumberOfOrdersRequestedPayload>

  @benchmarking.Getter(GET_NUMBER_OF_ORDERS_REQUESTED)
  numberOfOrdersRequested!: number

  device: string = Storage.get(Storage.items.DEVICE)
  NUMBER_OF_ORDERS_QUERY_PARAM = 'numberOfOrders'

  mounted() {
    this.setIsInBenchmarkingMode(true)
    updateMetaRefreshContent(
      `3600;url=/benchmarking?numberOfOrders=${this.numberOfOrdersRequested}`
    )
  }

  beforeDestroy() {
    this.setIsInBenchmarkingMode(false)
  }

  @Watch('$route', { immediate: true })
  onQueryChange(route: Route) {
    const query = route.query[this.NUMBER_OF_ORDERS_QUERY_PARAM]
    const requestedNumber = query
      ? Number(query)
      : DEFAULT_NUMBER_OF_REQUESTED_ORDERS
    if (requestedNumber != this.numberOfOrdersRequested) {
      this.setNumberOfOrdersRequested(requestedNumber)
    }
  }
}
